import React, {Component} from 'react';
import {connect} from 'react-redux';
import {login, logout,getUserData} from '../../reducer/reducer';
import {Redirect} from 'react-router-dom';
import {Button} from 'antd-mobile';
import axios from 'axios';

//获取reducer函数里的信息
const mapStateToProps = state => {
  return {state: state}
};
const actionCreators = {login, logout,getUserData};
// @connect(
//   state=>state,
//   {login,logout}
// )
//登录页
class Login extends Component {
  constructor() {
    super();
    this.state = {};
  }

  render() {
    return (
      <div>
        {this.props.state.flag ? <Redirect to='/dashboard'/> : null}
        <h2>我的名字是{this.props.state.user},年龄是{this.props.state.age}</h2>
        <p>你没有权限，需要登录才能看</p>
        <Button type='primary' onClick={this.props.login}>登录</Button>
      </div>
    )
  }
  componentDidMount(){
    this.props.getUserData()
  }
}

Login = connect(mapStateToProps, actionCreators)(Login);
export default Login;


