import React,{Component} from 'react';
import {Route,Link,Prompt} from 'react-router-dom';

//引入组件
import HomePage from '../homePage/HomePage';
import APage from '../aPage/aPage';
import BPage from '../bPage/bPage';
import TestPage from '../testPage/testPage';

//用户面板
class Dashboard extends Component{
  constructor(){
    super();
    this.state={};
  }
  render(){
    const match = this.props.match;
    console.log(this.props)
    return (
      <div>
        <h1>Dashboard</h1>
        <ul>
          <li>
            <Link to={`${match.url} /`}>首页</Link>
          </li>
          <li>
            <Link to={`${match.url}/aPage`}>第二页</Link>
          </li>
          <li>
            <Link to={`${match.url}/bPage`}>第三页</Link>
          </li>
        </ul>
        {/*<Prompt when={true} message="您确定要离开当前页面吗？"/>*/}
        <div>
          <Route path='/dashboard/' exact component={HomePage}></Route>
          <Route path='/dashboard/aPage' component={APage}></Route>
          <Route path='/dashboard/bPage' component={BPage}></Route>
        </div>
      </div>
    )
  }
}
export default Dashboard;


